import React, { createContext, useState, useEffect } from "react";
import axios from "axios";

// Tworzymy kontekst
export const MoodContext = createContext();

// Provider dla MoodContext
export const MoodProvider = ({ children }) => {
  const [moods, setMoods] = useState([]); // Stan dla nastrojw
  const [loading, setLoading] = useState(true); // Stan adowania
  const [error, setError] = useState(null); // Stan bdw

  const API_URL = process.env.REACT_APP_API_URL || "https://mood-for-anime-443a0efbedff.herokuapp.com";

  useEffect(() => {
    // Funkcja do pobierania nastrojw z backendu
    const fetchMoods = async () => {
      try {
        console.log(" Pobieranie nastrojw z:", `${API_URL}/api/moods`);
        const response = await axios.get(`${API_URL}/api/moods`);
        setMoods(response.data);
        setLoading(false);
      } catch (err) {
        console.error(" Bd podczas pobierania nastrojw:", err);
        setError("Nie udao si pobra nastrojw.");
        setLoading(false);
      }
    };

    fetchMoods(); // Wywoanie funkcji
  }, [API_URL]);

  return (
    <MoodContext.Provider value={{ moods, loading, error }}>
      {children}
    </MoodContext.Provider>
  );
};
